import WaterfallsFlowItem from '../WaterfallsFlowItem/WaterfallsFlowItem.vue'

export default {
	components:{
		WaterfallsFlowItem
	},
	props:{
		// 瀑布流列表
		wfList:{
			type:Array,
			require:true
		},
		updateNum:{
			type:Number,
			default:10
		}
	},
	data() {
		return {
			allList:[],       // 全部列表
			leftList:[],      // 左边列表
			rightList:[],     // 右边列表
			mark:0,           // 列表标记
			elHeight:0,
			boxHeight:[],     // 下标0和1分别为左列和右列高度
		};
	},
	watch:{
		async wfList(){
			if(this.wfList.length === 0 ||
				(this.wfList.length === this.updateNum && this.wfList.length <= this.allList.length)){
				this.allList = [];
				this.leftList = [];
				this.rightList = [];
				this.boxHeight = [];
				this.mark = 0;
			}

			if(this.wfList.length){
				this.allList = this.wfList;
				// console.log("将wflist里面的值给alllist")
				// console.log(this.allList)
				await this.waterFall()
			}
		},
		async mark(){
			const len = this.allList.length;
			if(this.mark < len && this.mark !== 0){
				await this.waterFall();
			}
		}
	},
	created() {
		// const query = uni.createSelectorQuery().in(this);
		// query.select("#left").boundingClientRect((res) => {
		// 	console.log(res)
		// })
		// this.$emit('getHeight' , this.elHeight);
	},
	methods:{
		async waterFall(){
			const i = this.mark;
			// console.log("康康这是啥："+i)
			if(i === 0){
				this.leftList.push(this.allList[i]);
				// console.log("这是存左边列表的数组：")
				// console.log(this.leftList)
				await this.getViewHeight(0);
			}else if(i === 1){
				this.rightList.push(this.allList[i]);
				// console.log("这是存右边列表的数组：")
				// console.log(this.rightList)
				await this.getViewHeight(1);
			}else {
				// console.log("判断第一个值是否大于第二个值")
				const leftOrRight = this.boxHeight[0] > this.boxHeight[1] ? 1 : 0;
				
				if(this.boxHeight[0]>this.boxHeight[1]){
					this.elHeight = this.boxHeight[0]
				}else{
					this.elHeight = this.boxHeight[1]
				}
				
				// console.log(leftOrRight)
				if(leftOrRight){this.rightList.push(this.allList[i])}
				else{this.leftList.push(this.allList[i])}
				await this.getViewHeight(leftOrRight);
			}
		},
		async getViewHeight(leftOrRight){
			const query = uni.createSelectorQuery().in(this);
			const id = leftOrRight ? '#right' : '#left';
			await setTimeout(()=>{
				query.select(id).boundingClientRect(res=>{
					this.boxHeight[leftOrRight] = res.height;
					// console.log("这里存的是高度：")
					// console.log(this.boxHeight)
					this.mark = this.mark + 1;
				}).exec();
			},50)
		},
		fn(){
			const timeId = setTimeout(() => {
				console.log(this.elHeight)
			   this.$emit('getHeight' , this.elHeight);
			    clearTimeout(timeId);
			}, 2000);
			
		},
		itemTap(item){
			this.$emit('itemTap',item)
		}
	},
	mounted() {
		this.fn();
	}
}
